//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"

// 3  
function numberWithCommas(x) { return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");}

export default function ShopPayPrice({openTermModal,priceInfo, usePoint, SetResultPrice, shipPrice}) {
    SetResultPrice(priceInfo.totalPrice - usePoint)

    return (
        <Container>
            <ShopPayPriceContainer>
                <ShopPayInfoWrap>
                    <ShopPayInfoTitleWrap>
                        <ShopPayInfoTitle></ShopPayInfoTitle>
                    </ShopPayInfoTitleWrap>
                    <ShopPayInfoInner>
                        <ShopPayInfoBox>
                            <ShopPayInfoBoxTitle>  </ShopPayInfoBoxTitle>
                            <ShopPayInfoBoxText>{numberWithCommas(priceInfo.originalPrice)}</ShopPayInfoBoxText>
                        </ShopPayInfoBox>
                        <ShopPayInfoBox>
                            <ShopPayInfoBoxTitle> </ShopPayInfoBoxTitle>
                            <ShopPayInfoBoxText>- {numberWithCommas(priceInfo.salePrice)}</ShopPayInfoBoxText>
                        </ShopPayInfoBox>
                        <ShopPayInfoBox>
                            <ShopPayInfoBoxTitle> </ShopPayInfoBoxTitle>
                            <ShopPayInfoBoxText>- {numberWithCommas(usePoint)}</ShopPayInfoBoxText>
                        </ShopPayInfoBox>
                        <ShopPayInfoBox>
                            <ShopPayInfoBoxTitle></ShopPayInfoBoxTitle>
                            <ShopPayInfoBoxText>{numberWithCommas(priceInfo.totalPrice - usePoint > 50000 ? "0" : shipPrice)}</ShopPayInfoBoxText>
                        </ShopPayInfoBox>
                        <Line></Line>
                        <ShopPayInfoBox>
                            <ShopPayTotalTitle>  </ShopPayTotalTitle>
                            <ShopPayTotalText>
                                {numberWithCommas(priceInfo.totalPrice - usePoint > 50000 ? priceInfo.totalPrice - usePoint : priceInfo.totalPrice - usePoint + shipPrice)}
                            </ShopPayTotalText>
                        </ShopPayInfoBox>
                    </ShopPayInfoInner>
                </ShopPayInfoWrap>
            </ShopPayPriceContainer>
            <TermWrap>
                <TermText onClick={()=>{openTermModal(true)}}>       </TermText>
            </TermWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    padding-bottom: calc(100vw*(45/414));
`
const ShopPayPriceContainer = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0, 0, 0, 0.16);
    z-index: 10;
    margin-top: calc(100vw*(10/414));
`
const ShopPayInfoWrap = styled.div`
    width: 100%;
    padding-top: calc(100vw*(20/414));
    padding: calc(100vw*(24/414)) calc(100vw*(11/414)) calc(100vw*(22/414));
`
const ShopPayInfoTitleWrap = styled.div`
    width: 100%;
    margin-bottom: calc(100vw*(20/414));
    padding: 0 calc(100vw*(25/414));
`
const ShopPayInfoTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
    margin-bottom: calc(100vw*(5/414));
`
const ShopPayInfoInner = styled.div`
    width: 100%;
`
const ShopPayInfoBox = styled.div`
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    padding: calc(100vw*(12/414)) calc(100vw*(25/414));
`
const ShopPayInfoBoxTitle = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
`
const ShopPayInfoBoxText = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    text-align: right;
`
const Line = styled.div`
    width: 100%; height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin-top: calc(100vw*(10/414));
`
const ShopPayTotalTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
    color: #ed2939;
`
const ShopPayTotalText = styled(ShopPayTotalTitle)`
    text-align: right;
`
const TermWrap = styled.div`
    width: 100%;
`
const TermText = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    text-align: center;
    color: #969696;
    padding: calc(100vw*(10/414)) 0;
`