//css
import styled from "styled-components"


const GuideInfoList = [
    {
        shopping_method: "//",
        shopping_price1:"",
        shopping_price2:"-15,000    ",
        shopping_price3:"-    ",
        bundle_shipping:"",
        delivery_period:"-   (,         ,         .)",
        return_cost:"  ",
        return_method:" //      ",
    }
]

export default function ShopProductReview() {

    return (
        <Container>
            <ShopLine></ShopLine>

            <GuideWrap>
                <ReviewInnerWrap>
                    {
                        GuideInfoList.map((value)=>{
                            
                            return (
                                <div>
                                    <GuideboxWrap>
                                        <GuideTitleWrap>
                                            <GuideTitle></GuideTitle>
                                        </GuideTitleWrap>
                                        <>
                                            <table>
                                                <tr>
                                                    <TableTitle></TableTitle>
                                                    <TableTextWrap>
                                                        <TableText>{value.shopping_method}</TableText>
                                                    </TableTextWrap>
                                                </tr>
                                                <tr>
                                                    <TableTitle></TableTitle>
                                                    <TableTextWrapA>
                                                        <TableText>{value.shopping_price1}</TableText>
                                                        <TableText>{value.shopping_price2}</TableText>
                                                        <TableText>{value.shopping_price3}</TableText>
                                                    </TableTextWrapA>
                                                </tr>
                                                <tr>
                                                    <TableTitle> </TableTitle>
                                                    <TableTextWrap>
                                                        <TableText>{value.bundle_shipping}</TableText>
                                                    </TableTextWrap>
                                                </tr>
                                                <tr>
                                                    <TableTitle style={{borderBottom: "calc(100vw*(1/414)) solid #e4e4e4"}}></TableTitle>
                                                    <TableTextWrap style={{borderBottom: "calc(100vw*(1/414)) solid #e4e4e4"}}>
                                                        <TableText>{value.delivery_period}</TableText>
                                                    </TableTextWrap>
                                                </tr>
                                            </table>
                                        </>
                                    </GuideboxWrap>
                                    <GuideboxWrap>
                                        <GuideTitleWrap>
                                            <GuideTitle>/ </GuideTitle>
                                        </GuideTitleWrap>
                                        <>
                                            <table>
                                                <tr>
                                                    <TableTitle>/ </TableTitle>
                                                    <TableTextWrap>
                                                        <TableText>{value.return_cost}</TableText>
                                                    </TableTextWrap>
                                                </tr>
                                                <tr>
                                                    <TableTitle style={{borderBottom: "calc(100vw*(1/414)) solid #e4e4e4"}}>/  </TableTitle>
                                                    <TableTextWrapA style={{borderBottom: "calc(100vw*(1/414)) solid #e4e4e4"}}>
                                                        <TableText>{value.return_method}</TableText>
                                                    </TableTextWrapA>
                                                </tr>
                                            </table>
                                        </>
                                    </GuideboxWrap>
                                </div>
                            )
                        })
                    }
                    <GuideboxWrap>
                        <GuideTitleWrap>
                            <GuideTitle>/ </GuideTitle>
                        </GuideTitleWrap>
                        <GuideDetailWrap>
                            <TableText>- /  ,     </TableText>
                            <TableText>-          
                              (,       )</TableText>
                            <TableText>-  , ,      
                             </TableText>
                            <TableText>-   ,      
                            //   </TableText>
                            <TableText>-        ,
                               /   </TableText>
                            <TableText>-   (  )       
                            /  </TableText>
                        </GuideDetailWrap>
                    </GuideboxWrap>
                </ReviewInnerWrap>
            </GuideWrap>
        </Container>
    );
}


const Container = styled.div`
    width: 100%;
`
const GuideWrap = styled.div`
    width: 100%;
    background: #fff;
    border-radius: calc(100vw*(15/414)) calc(100vw*(15/414)) 0 0;
    box-shadow: 0 0 calc(100vw*(6/414)) 0 rgba(0,0,0,0.16);
    padding-bottom: calc(100vw*(18/414));
`
const ShopLine = styled.div`
    width: 100%;
    height: calc(100vw*(14/414));
    background: #f7f7f7;
`
const ReviewInnerWrap = styled.div`
    width: 100%;
`
const GuideboxWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(40/414)) calc(100vw*(28/414)) calc(100vw*(8/414));
`
const GuideTitleWrap = styled.div`
    margin-bottom: calc(100vw*(20/414));
`
const GuideTitle = styled.h3`
    font-size: calc(100vw*(13/414));
`
const TableTitle = styled.th`
    width: calc(100vw*(127/414));
    background: #f7f7f7;
    font-size: calc(100vw*(13/414));
    font-weight: 900;
    text-align: center;
    padding: calc(100vw*(12/414)) 0;
    vertical-align: middle;
    border-top: calc(100vw*(1/414)) solid #e4e4e4;
    border-left: calc(100vw*(1/414)) solid #e4e4e4;
`
const TableTextWrap = styled.td`
    width: calc(100vw*(230/414));
    padding: calc(100vw*(12/414)) 0;
    vertical-align: middle;
    border: calc(100vw*(1/414)) solid #e4e4e4;
    border-bottom: none;
    font-size: calc(100vw*(13/414));
    padding: calc(100vw*(12/414)) calc(100vw*(12/414)) calc(100vw*(12/414)) calc(100vw*(11/414));
`
const TableText = styled.p`
    font-size: calc(100vw*(13/414));
    line-height: 1.5;
`
const TableTextWrapA = styled(TableTextWrap)`
    padding: calc(100vw*(30/414)) calc(100vw*(10/414)) calc(100vw*(30/414)) calc(100vw*(11/414));

`
const GuideDetailWrap = styled.div`
    margin-bottom: calc(100vw*(35/414));
`
