//react
import {Link} from "react-router-dom";
import {useState, useEffect} from 'react';

//css
import styled from "styled-components"


export default function PayDetail() {

    return (
        <Container>
            <PayDetailWrap>
                <PayDetailTitleWrap>
                    <PayDetailTitle>2020.12.30</PayDetailTitle>
                    <OrderNumber> 12345678901234</OrderNumber>
                </PayDetailTitleWrap>
                {/* info */}
                <PayDetailListWrap>
                    <PayDetailList>
                        <DetailListTitleWrap>
                            <DetailListTitle></DetailListTitle>
                        </DetailListTitleWrap>
                        <DetailListTextWrap>
                            <DetailListText></DetailListText>
                            <DetailListSubText>25,900</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText></DetailListText>
                            <DetailListSubText>-0</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText></DetailListText>
                            <DetailListSubText>0</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText></DetailListText>
                            <DetailListSubText>-0</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText></DetailListText>
                            <DetailListSubText>25,900</DetailListSubText>
                        </DetailListTextWrap>
                        <Line></Line>
                        <DetailListTextWrap>
                            <TotalDetailText></TotalDetailText>
                            <TotalDetailSubText>25,900</TotalDetailSubText>
                        </DetailListTextWrap>
                    </PayDetailList>
                </PayDetailListWrap>

                {/* point */}
                <PayDetailListWrap>
                    <PayDetailList>
                        <DetailListTitleWrap>
                            <DetailListTitle> </DetailListTitle>
                        </DetailListTitleWrap>
                        <DetailListTextWrap>
                            <DetailListText> </DetailListText>
                            <DetailListSubText>120</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText> </DetailListText>
                            <DetailListSubText>-0</DetailListSubText>
                        </DetailListTextWrap>
                    </PayDetailList>
                </PayDetailListWrap>

                {/* address */}
                <PayDetailListWrap>
                    <PayDetailList>
                        <DetailListTitleWrap>
                            <DetailListTitle></DetailListTitle>
                        </DetailListTitleWrap>
                        <DetailListTextWrap>
                            <DetailListText></DetailListText>
                            <DetailListSubText></DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText></DetailListText>
                            <DetailListSubText>   131-1  103 103</DetailListSubText>
                        </DetailListTextWrap>
                        <DetailListTextWrap>
                            <DetailListText></DetailListText>
                            <DetailListSubText>010-0000-0000</DetailListSubText>
                        </DetailListTextWrap>
                        <MessageTextWrap>
                            <DetailListSubText> .</DetailListSubText>
                        </MessageTextWrap>
                    </PayDetailList>
                </PayDetailListWrap>
            </PayDetailWrap>
        </Container>
    );
}

//css
const Container = styled.div` 
    position: relative;
    width: 100%;
    background: #fff;
    z-index: 10;
`
const PayDetailWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(25/414)) calc(100vw*(15/414)) 0;
`
const PayDetailTitleWrap = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 calc(100vw*(15/414));
`
const PayDetailTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const OrderNumber = styled.p`
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #979797;
`
const PayDetailListWrap = styled.div`
    width: 100%;
    margin-top: calc(100vw*(18/414));
`
const PayDetailList = styled.div`
    width: 100%;
    border: calc(100vw*(1/414)) solid #dddddc;
    border-radius: calc(100vw*(12/414));
    padding: calc(100vw*(10/414)) calc(100vw*(15/414)) calc(100vw*(25/414));
`
const DetailListTitleWrap = styled.div`
    width: 100%;
    padding: calc(100vw*(10/414)) calc(100vw*(15/414));
`
const DetailListTitle = styled.p`
    font-size: calc(100vw*(18/414));
    font-weight: bold;
`
const DetailListTextWrap = styled.div`
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    width: 100%;
    padding: calc(100vw*(8/414)) calc(100vw*(15/414));
`
const DetailListText = styled.p`
    width: calc(100vw*(90/414));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #979797;
`
const TotalDetailText = styled(DetailListText)`
    color: #ed2939;
`
const DetailListSubText = styled.p`
    width: calc(100% - calc(100vw*(90/414)));
    font-size: calc(100vw*(13/414));
    font-weight: bold;
    color: #4b4b4b;
    text-align: right;
    word-break: keep-all;
`
const TotalDetailSubText = styled(DetailListSubText)`
    color: #ed2939;
`
const Line = styled.div`
    width: 100%; height: calc(100vw*(1/414));
    background: #d1d1d1;
    margin-top: calc(100vw*(8/414));
`
const MessageTextWrap = styled.div`
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    padding: calc(100vw*(8/414)) calc(100vw*(15/414));
`